$(function() {
	//
	$(window).scrollTop(0);
	
	/*banner*/
	$(".carousel").carousel({
		interval: 3000
	});
	
	//css
	animate();

})


//
function initMap() {
	createMap(); //
	setMapEvent(); //
	addMapControl(); //
	addMarker(); //marker
}

//
function createMap() {
	var map = new BMap.Map("dituContent"); //
	var point = new BMap.Point(108.960518, 34.198179); //
	map.centerAndZoom(point, 18); //
	window.map = map; //map
}

//
function setMapEvent() {
	map.enableDragging(); //()
	map.enableScrollWheelZoom(); //
	map.enableDoubleClickZoom(); //()
	map.enableKeyboard(); //
}

//
function addMapControl() {}

//
var markerArr = [{
	title: "",
	content: "",
	point: "108.960504|34.19809",
	isOpen: 1,
	icon: {
		w: 23,
		h: 25,
		l: 46,
		t: 21,
		x: 9,
		lb: 12
	}
}];
//marker
function addMarker() {
	for(var i = 0; i < markerArr.length; i++) {
		var json = markerArr[i];
		var p0 = json.point.split("|")[0];
		var p1 = json.point.split("|")[1];
		var point = new BMap.Point(p0, p1);
		var iconImg = createIcon(json.icon);
		var marker = new BMap.Marker(point, {
			icon: iconImg
		});
		var iw = createInfoWindow(i);
		var label = new BMap.Label(json.title, {
			"offset": new BMap.Size(json.icon.lb - json.icon.x + 10, -20)
		});
		marker.setLabel(label);
		map.addOverlay(marker);
		label.setStyle({
			borderColor: "#808080",
			color: "#333",
			cursor: "pointer"
		});

		(function() {
			var index = i;
			var _iw = createInfoWindow(i);
			var _marker = marker;
			_marker.addEventListener("click", function() {
				this.openInfoWindow(_iw);
			});
			_iw.addEventListener("open", function() {
				_marker.getLabel().hide();
			})
			_iw.addEventListener("close", function() {
				_marker.getLabel().show();
			})
			label.addEventListener("click", function() {
				_marker.openInfoWindow(_iw);
			})
			if(!!json.isOpen) {
				label.hide();
				_marker.openInfoWindow(_iw);
			}
		})()
	}
}
//InfoWindow
function createInfoWindow(i) {
	var json = markerArr[i];
	var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>" + json.content + "</div>");
	return iw;
}
//Icon
function createIcon(json) {
	var icon = new BMap.Icon("http://app.baidu.com/map/images/us_mk_icon.png", new BMap.Size(json.w, json.h), {
		imageOffset: new BMap.Size(-json.l, -json.t),
		infoWindowOffset: new BMap.Size(json.lb + 5, 1),
		offset: new BMap.Size(json.x, json.h)
	})
	return icon;
}

//
function animate() {
	var a, b, c;
	a = $(window).height();
	if(document.body.offsetWidth <= 800) {
			$(".page-wrapper li:nth-child(2)").find(".business-titleZ,.business-titleY").addClass("title-animate");	
			$(".page-wrapper li:nth-child(2)").find(".businessTop-left").addClass("animation_img");
	}
	$(window).scroll(function() {
		var b = $(this).scrollTop();
		$(".page-wrapper li").each(function() {
			c = $(this).offset().top;
			if(a + b > c) {
				$(this).find(".businessTop-left,.businessBottom-right").addClass("animation_img");
				setTimeout(()=>{
					$(this).find(".business-titleZ,.business-titleY,.business-info h5,.business-info h6,.business-info p,.resource-info h5,.resource-info p,.iconBox p").addClass("title-animate");
				},1000)
				setTimeout(() => {
					$(this).find(".vanish_btn").addClass("vanish-animate");
				}, 2000)			
			}
		});
	});
}


