<template>
  <div id="cultureCreativity">
    <div id="wrap">
      <!--  -->
      <header-mobile v-show="!isPc"></header-mobile>
      <header-two v-show="isPc"></header-two>
      <!--  -->
      <div class="main">
        <div class="margin_top">
          <img src="../../img/wenhuachuangyipingtai_banner01.jpg">
        </div>
        <div class="main-container">
          <div class="container animateList">
            <h6 class="cultureTitle"></h6>
            <div class="ultureText">
              +IPIP
            </div>
            <div class="cultureImg">
              <img src="../../img/wenhuachuangyipingtai_pic001.png">
            </div>
          </div>
          <div class="cultureInfo animateList">
            <div class="container">
              <div class="icon_elememt">
                <img src="../../img/icon_elememt.png">
              </div>
              <h6 class="cultureTitle">  APP</h6>
              <div class="ultureText">
                
              </div>
              <div class="cultureImg">
                <img src="../../img/wenhuachuangyipingtai_pic002.jpg">
              </div>
            </div>
            <div class="web_bg">
              <div class="mobileImg">
                <img src="../../img/wenhuachuangyi_pic_mobile001.png">
              </div>
            </div>
          </div>
          <div class="cultureInfo animateList">
            <div class="container">
              <div class="icon_elememt">
                <img src="../../img/icon_elememt.png">
              </div>
              <h6 class="cultureTitle"> VLOG APP</h6>
              <div class="ultureText">
                vlog VLOG---- 
              </div>
              <div class="cultureImg">
                <img src="../../img/wenhuachuangyipingtai_pic003.png">
              </div>
            </div>
            <div class="web_bg">
              <div class="mobileImg">
                <img src="../../img/wenhuachuangyi_pic_mobile002.png">
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <footer-two></footer-two>
    </div>
  </div>
</template>

<script>
  import { initShare } from '@/utils/wechatShare.js'
  import "../../common/js/cultureCreativity.js";
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";

  export default {
    data() {
      return {
        isPc: true
      };
    },
    mounted() {
      this.setWidth()

const shareData = {
      title: '',
      desc: '',
      link: location.href,
      imgUrl:'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/cultrul111.png.png',
      signUrl: encodeURIComponent(location.href)
    }
    initShare(shareData)
    },
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false
        }
      }
    },
    components: {
      headerTwo,
      footerTwo,
      headerMobile
    }
  };

</script>

<style scoped>
  @import "../../common/css/cultureCreativity.css";

  @media screen and (max-width: 992px) {
    .margin_top img {
      width: 200% !important;
      margin-left: -50%;
    }
    .margin_top{
      overflow: hidden;
    }
  }

</style>



