    <!---->
    <div class="main">
      <div class="topBanner">
        <img src="../../img/guanyuwomen_pic001.jpg">
      </div>
      <div class="infoBox">
        <div class="topContent row">
          <div class="topContentLeft col-md-6">
            <p></p>
            <p class="textInfo"> </p>
            <p></p>
            <p class="textInfo"> </p>
          </div>
          <div class="topContentRight col-md-6">
            <span></span> 201512
          </div>
          <div class="clearfix"></div>
          <div
            class="textDetail"
          >+</div>
        </div>

        <div class="photoList">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText"></p>
            <p class="titleEn">Proffessional Team</p>
          </div>
          <div class="photoBox row">
            <div class="col-md-12 imgBox">
              <img src="../../img/guanyuwomen_pic002.png">
            </div>
          </div>
        </div>

