          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText"></p>
            <p class="titleEn">environment</p>
          </div>
          <div class="photoBox row" id="photoBox">
            <div @mouseenter="setImgGray(index+1)" @mouseleave="setImgNomal(index+1)" class="col-xs-6 col-md-4 col-lg-3 imgBox" v-for="(item,index) in componyImg" :key="index">
              <img class="gray" :src="item.imgUrl">
            </div>
           
          </div>
        </div>

        <div class="photoList">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText"></p>
            <p class="titleEn">Contant Us</p>
          </div>
          <div class="photoBox row mapBox">
            <div class="col-md-12 dituBox">
              <div id="dituContent"><baidu-map></baidu-map></div>
              <!-- <map></map> -->
              <div class="rightContactInfo">
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_mail@2x.png">
                  <p>zjys@zhongjingchina.cn</p>
                </div>
                <div class="lineBox"></div>
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_location@2x.png">
