import wx from 'weixin-js-sdk'
import axios from 'axios'

export function initShare(shareData) {
  var ua = window.navigator.userAgent.toLowerCase()
  // console.log(ua.match(/MicroMessenger/i)=='micromessenger')
  // debugger

  /* eslint-disable */
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
      /* eslint-enable */
      console.log(process.env)
    axios.get(process.env.BASE_API + 'wechat/getShareInfo?url=' + shareData.signUrl)
    .then(function(response) {
      const data = response.data
      // debugger;

      if (!data.hasError && data.code === 200) {
        const result = data.results
        wx.config({
          debug: false, // ,apialert
          appId: result.appId, // 
          timestamp: result.timestamp, // 
          nonceStr: result.noncestr, // 
          signature: result.signature, // 
          jsApiList: ['checkJsApi', 'onMenuShareTimeline',
            'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone', 'hideMenuItems']
        })
        wx.ready(function() {
          wx.onMenuShareTimeline({
            title: shareData.title, // 
            desc: shareData.desc, // 
            link: shareData.link, // 
            imgUrl: shareData.imgUrl, // 
            success: function() {
              // 
            },
            cancel: function() {
              // 
            }
          })
          // 
          wx.onMenuShareAppMessage({
            title: shareData.title, // 
            desc: shareData.desc, // 
            link: shareData.link, // 
            imgUrl: shareData.imgUrl, // 
            type: 'link', // ,musicvideolinklink
            dataUrl: '', // typemusicvideo
            success: function() {
              // 
            },
            cancel: function() {
              // 
            }
          })
          wx.onMenuShareQQ({
            title: shareData.title, // 
            desc: shareData.desc, // 
            link: shareData.link, // 
            imgUrl: shareData.imgUrl, // 
            success: function(res) {

            },
            cancel: function(res) {

            }
          })
          wx.onMenuShareWeibo({
            title: shareData.title, // 
            desc: shareData.desc, // 
            link: shareData.link, // 
            imgUrl: shareData.imgUrl, // 
            success: function(res) {

            },
            cancel: function(res) {

            }
          })
          wx.onMenuShareQZone({
            title: shareData.title, // 
            desc: shareData.desc, // 
            link: shareData.link, // 
            imgUrl: shareData.imgUrl, // 
            success: function(res) {

            },
            cancel: function(res) {

            }
          })
          wx.hideMenuItems({
            menuList: ['menuItem:originPage', // 
              'menuItem:copyUrl' // 
            ]
          })
        })
        wx.error(function(res) {
          console.log('', res)
        })
        wx.checkJsApi({
          jsApiList: ['onMenuShareTimeline',
            'onMenuShareAppMessage', 'hideMenuItems'], // JSJS2,
          success: function(res) {
            // alert(":"+res.err_msg);
          }
        })
      }
    })
    .catch(error=>{
      console.log(error)
    })
  }
}



